	 import {
	 	mapState
	 } from 'vuex';
	 import service from '../../service.js';
	 import mInput from '../../components/m-input.vue';
	 const BASE_URL = 'http://www.luominus.com/';
	 import uniRequest from 'uni-request';
 	export default {
		computed: mapState(['forcedLogin', 'hasLogin', 'userName','avatarUrl']),
 		components: {
 			// sunTab
 		},
 		data() {
 			return {
 				menus:{
 					"group_name": " ",
					"referrer_code": " ",
					"all": 0,
					"my_all": 0,
					"today_price": 0,
					"today_income": 0,
					"week_price": 0,
					"week_income": 0,
					"last_week_price": 0,
					"last_week_income": 0
 				}
 			}
 		},
 		onLoad() {
 				if (!this.hasLogin) {
					uni.showLoading({
					    title: '加载中'
					});
 						let validUser = service.getUsers();
 						if (validUser.length !== 0) {
 							// 存在缓存数据getUsers（）对象
 							console.log(validUser[0].token)
 							
							const newData = {
								token:validUser[0].token
							}
 							setTimeout(function () {
 							    uni.hideLoading();
 							}, 2000);
 							uni.request({  
 								url:BASE_URL + "api/v1/Index/groupData",  
 								data: newData,  
 								method:'GET',  
 								dataType:'json',  
 								header:{  
 									'content-type':'application/json'  
 								},  
 								success: (e) =>{  
 									console.log(e) 
 									 if (e.statusCode === 200) {
 									 	if (e.data.code === 200) {
 									 		var myData = e.data.data
 									 		this.menus = {
 									 			"group_name": myData.group_name,
												"referrer_code": myData.referrer_code,
												"all": myData.all,
												"my_all": myData.my_all,
												"today_price": myData.today_price,
												"today_income": myData.today_income,
												"week_price": myData.week_price,
												"week_income": myData.week_income,
												"last_week_price": myData.last_week_price,
												"last_week_income": myData.last_week_income
 									 		}
 									 		console.log(this.menus)
 									 	}
 									 }
 								},  
 							})
 						} else {
 							uni.showModal({
 								title: '未登录',
 								content: '您未登录，需要登录后才能继续',
 								/**
 								 * 如果需要强制登录，不显示取消按钮
 								 */
 								showCancel: !this.forcedLogin,
 								success: (res) => {
 									if (res.confirm) {
 										/**
 										 * 如果需要强制登录，使用reLaunch方式
 										 */
 										if (this.forcedLogin) {
 											uni.reLaunch({
 												url: '../login/login'
 											});
 										} else {
 											uni.navigateTo({
 												url: '../login/login'
 											});
 										}
 									}
 								}
 							});
 						}	
 						
 				}
 			}
 		,
 		methods: {
			myTeam() {
				console.log(1)
				uni.navigateTo({
					url: '../team/team',
				});
			}
 		}
 	}
 